<!--  -->
<template>
    <div class='main'>
        <div class='top'>
            <img src="../../../static/image/main-banner.png" alt="" class='banner' >
            <div class='topBox'>
                <img src="../../../static/image//main-photo.png" alt="" class='avatar' >
                <div class='top-fr'>
                    <div class='top-name'>点击头像登录注册</div>
                    <div class='top-bm'>
                        <div class='top-list'>卡券</div>
                        <div class='top-list'>门票</div>
                    </div>
                </div>
            </div>
        </div>
        <div class='order'>
            <div class='order-title' @click='orderName(0)'>
                <span>我的订单</span>
                <img src="../../../static/image/more.png" alt="" class='more-icon'>
            </div>
            <div class='order-box'>
                <div class='order-list'  @click='orderName(1)'>
                    <img src="../../../static/image/order-icon1.png" alt="" class='order-icon' >
                    <p class='order-text'>待付款</p>
                </div>
                <div class='order-list'  @click='orderName(2)'>
                    <img src="../../../static/image/order-icon2.png" alt="" class='order-icon' >
                    <p class='order-text'>待发货</p>
                </div>
                <div class='order-list'  @click='orderName(3)'>
                    <img src="../../../static/image/order-icon3.png" alt="" class='order-icon' >
                    <p class='order-text'>待收货</p>
                </div>
                <div class='order-list'  @click='orderName(4)'>
                    <img src="../../../static/image/order-icon4.png" alt="" class='order-icon' >
                    <p class='order-text'>已完成</p>
                </div>
                 <div class='order-list'>
                    <img src="../../../static/image/order-icon5.png" alt="" class='order-icon' >
                    <p class='order-text'>收货地址</p>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    //这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
    //例如：import 《组件名称》 from '《组件路径》';

    export default {
    //import引入的组件需要注入到对象中才能使用
    components: {},
    data() {
    //这里存放数据
    return {

    };
    },
    //监听属性 类似于data概念
    computed: {

    },
    //监控data中的数据变化
    watch: {},
    //方法集合
    methods: {
        // 跳转订单详情
        orderName:function(type){
            this.$router.push({
                name:'order',
                params:{
                    type:type
                }
            })
        }
    },
    //生命周期 - 创建完成（可以访问当前this实例）
    created() {
            this.$store.dispatch('headTitle','我的')
            this.$store.dispatch('goBack',false)
            this.$store.dispatch('showBtm',true)
    },
    //生命周期 - 挂载完成（可以访问DOM元素）
    mounted() {

    },
    beforeCreate() {}, //生命周期 - 创建之前
    beforeMount() {}, //生命周期 - 挂载之前
    beforeUpdate() {}, //生命周期 - 更新之前
    updated() {}, //生命周期 - 更新之后
    beforeDestroy() {}, //生命周期 - 销毁之前
    destroyed() {}, //生命周期 - 销毁完成
    activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
    }
</script>
<style lang='scss' scoped>
    .main{
        width:100%;
        padding-top:90px;
    }
    .top{
        width:100%;
        position: relative;
        margin-bottom:30px;
    }
    .banner{
        width:100%;
        height:300px;
        display: block;
    }
    .topBox{
        width:90%;
        height:100%;
        position: absolute;
        left:5%;
        top:0;
        display:flex;
        align-items:center;
    }
    .avatar{
        width:150px;
        height:150px;
        border-radius:50%;
        margin-right:30px;
    }
    .top-fr{
        width:400px;
    }
    .top-name{
        font-size:38px;
        color:#fff;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        margin-bottom:20px;
        text-align:left;
    }
    .top-bm{
        display:flex;
    }
    .top-list{
        padding:0 20px;
        height:40px;
        border-bottom-right-radius:15px;
        border-top-left-radius: 15px;
        line-height:40px;
        display:block;
        font-size:28px;
        color:#fff;
        background:rgba(255,255,255,0.3);
        margin-right:20px;
    }
    .order{
        width:94%;
        margin:0 auto;
        background:#fff;
    }
    .order-title{
        padding:0 3%;
        height:88px;
        display:flex;
        justify-content:space-between;
        align-items: center;
         border-bottom:1px solid rgb(241,242,246);
    }
    .order-title span{
        font-size:30px;
        color:#000;
    }
    .more-icon{
        width:20px;
        height:20px;
    }
    .order-box{
        width:100%;
        height:140px;
        display:flex;
        justify-content:space-around;
        align-items:center;
    }
    .order-list{
        display:flex;
        justify-content:center;
        flex-direction:column;
        position: relative;
    }
    .order-icon{
        width:52px;
        height:52px;
        display:block;
        margin:0 auto;
    }
    .order-text{
        width:100%;
        text-align:center;
        font-size:22px;
        color:#000;
    }
</style>


